body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.quote-wrapper{
  background:url('https://www.transparenttextures.com/patterns/arabesque.png');
  border:10px solid #9e9e9e;
  height:calc(100vh - 20vh);
  margin:10vh auto;
  position: relative;
  width:50%;
}
.loaded{
  display: none;
}
.loader-line{
  width:80%;
  height:15px;
  background:#ccc;
  margin-bottom:10px;
}
.loader-line-2{
  width:70%;
}
.loader-line-3{
  width:55%;
}
.loader-line-4{
  width:50%;
}
.quote-title{
  background: #9e9e9e;
  color:#fff;
  font-size:35px;
  position: absolute;
  top:-30px;
  text-transform: capitalize;
  left:0;
  right:0;
  margin:0 auto;
  padding:10px;
  width:70%;
}
blockquote{
  color: #000;
  margin-top:100px;
  font-size:35px;
  font-weight: bold;
  text-align: left;
}
.author{
  text-align: left;
  margin-left:40px;
  font-size:20px;
}
@media only screen and (max-width:767px){
  .quote-wrapper{
    width:100%;
    box-sizing: border-box;
    margin:20px 0 0 0;
    height: auto;
  }
  blockquote{
    font-size: 22px;
    margin-top:20px;
  }
  .quote-title{
    width:100%;
    font-size:25px;
    padding:10px;
    position: relative;
    top:-10px;
    margin:0;
  }
}
@media only screen and (min-width:768px) and (max-width:1024px){
  .quote-wrapper{
    width:80%;
  }
  .quote-title{
    font-size:30px;
  }
  blockquote{
    font-size: 28px;
  }
}
